@keyframes hvr-ripple-out {
    0% {
        top: -5px;
        right: -5px;
        bottom: -5px;
        left: -5px;
    }
    50% {
        top: -15px;
        right: -15px;
        bottom: -15px;
        left: -15px;
        opacity: 0.8;
    }

    100% {
        top: -5px;
        right: -5px;
        bottom: -5px;
        left: -5px;
        opacity: 1;
    }
}
@keyframes hvr-ripple-out-two {
    0% {
        top: 0;
        right: 0;
        bottom: 0;
        left: 0;
    }
    100% {
        top: -30px;
        right: -30px;
        bottom: -30px;
        left: -30px;
        opacity: 0;
    }
}

