#codeconSkills {
	font-size: 20px;
	height: auto;
	margin: 0 auto;
	padding: 10px;
	width: 75%;
}
#codeconSkills img {
	width: 50px;
	height: 50px;

	position: absolute;
	right: -70px;
	display: none;
}
.codeconSkillbar {
	height: 50px;
	position: relative;
	background: rgba(17, 17, 17, 0.3);
}
#codeconHTML {
	width: 100%;
	animation: Animate-HTML 4s;
	-webkit-animation: Animate-HTML 4s;
	-moz-animation: Animate-HTML 4s;
	-o-animation: Animate-HTML 4s;
	height: 50px;
	position: absolute;
	background-color: #ea8564;
}
@keyframes Animate-HTML {
	from {
		width: 10px;
	}
	to {
		width: 100%;
	}
}
@-webkit-keyframes Animate-HTML {
	from {
		width: 10px;
	}
	to {
		width: 100%;
	}
}
@-moz-keyframes Animate-HTML {
	from {
		width: 10px;
	}
	to {
		width: 100%;
	}
}
@-o-keyframes Animate-HTML {
	from {
		width: 10px;
	}
	to {
		width: 100%;
	}
}
